.background-container {
    margin: 0;
    padding: 0;
    /*background-color:#6abadeba;*/
    background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/142996/slider-2.jpg');
    font-family: 'Arial';
    height: 100%;
    background-size:     cover;                      /* <------ */
    background-repeat:   no-repeat;
    background-position: center center;
}
.form-wrapper{
    width: 382px;
    height: 100%;
    overflow: hidden;
    margin: auto;
    margin: 20 0 0 450px;
    padding-left: 80px;
    padding-right: 80px;
    background: linear-gradient(to bottom, rgba(146, 135, 187, 0.8) 0%, rgba(0,0,0,0.6) 100%);
    border-radius: 15px ;

}

.login-logo {
    width: 100%;
    height: 200px;
    margin-bottom: 3rem;
}
.login-form {
    width: 100%;
}
.login-row {
    width: 100%;
    height: 2rem;
    white-space: nowrap;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    margin-bottom: 5rem;
    margin-top: 1.5rem;
}

.login-icon {
    width: 32px;
    display: inline-block;
    fill: none;
    stroke: whitesmoke;
}.login-icon {
    width: 32px;
    display: inline-block;
    fill: none;
    stroke: whitesmoke;
}

.login-button {
    position: relative;
    width: 100%;
    height: 4rem;
    margin: 1rem 0 1rem;
    color: rgba(255,255,255,0.8);
    background: #31a8ae;
    font-size: 1.5rem;
    border-radius: 3rem;
    cursor: pointer;
    overflow: hidden;
    border-color: transparent;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;

}

