.login-input{
    box-sizing: border-box;
    width: 100%;
    border: none;
    border-radius: 3px;
    padding-left: 8px;
    background: white;
    position: relative;
    margin-left: 1rem;
    background: transparent;
    font-family: "Franklin Gothic Medium";
    font-size: x-large;
    color: whitesmoke;
