.login-p {
    font-size: 1.2rem;
    color: #ABA8AE;
    text-align: center;
    width: max-content;
    margin: 0;
    display: inline-block;
}

#login-row-register {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    border: none;
    margin-top: 3rem;
}

.login-register {
    color: #fff;
    cursor: pointer;
    text-decoration: none;
    background: transparent;
    margin-left: 0.5rem;
}
#pass{
    width: 300px;
    height: 30px;
    border: none;
    border-radius: 3px;
    padding-left: 8px;
}

#loginButton{
    width: 300px;
    height: 30px;
    border: none;
    border-radius: 17px;
    padding-left: 7px;
    margin-top: 10px;
    color: blue;
}
