.register-form {
    width: 100%;
}
.register-row {
    width: 100%;
    height: 2rem;
    white-space: nowrap;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    margin-bottom: 5rem;
    margin-top: 1.5rem;
    align-items: center;
}
#upload-form {
    height: 50%;
    width: 50%;
}
.form-p {
    color: whitesmoke;
    font-family: "Franklin Gothic Medium";
    font-size: x-large;
}
.form-input{
    box-sizing: border-box;
    width: 100%;
    height: 3rem;
    border: none;
    border-radius: 3px;
    padding-left: 8px;
    background: white;
    position: relative;
    margin-left: 1rem;
    background: transparent;
    font-family: "Franklin Gothic Medium";
    font-size: x-large;
    color: whitesmoke;
}

.form-button {
    position: relative;
    width: 100%;
    height: 4rem;
    margin: 1rem 0 1rem;
    color: rgba(255,255,255,0.8);
    background: #31a8ae;
    font-size: 1.5rem;
    border-radius: 3rem;
    cursor: pointer;
    overflow: hidden;
    border-color: transparent;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;

}

.register-logo {
    width: 100%;
    height: 200px;
    margin-bottom: 3rem;
}