#profile-name{
    padding: 0;
    margin: 0;
}

#profile-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#profile-wrapper{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
    overflow: auto;
    width: 80%;
    padding-left: 3rem;
    padding-bottom: 3rem;
    padding-right: 0;
    margin-left: 5rem;
    margin-right: 5rem;
}

.profile-video {
    width:100%;
}

.profile-video-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 15rem;
    margin: 3rem 3rem 0 0;
}

.profile-p {
    color: whitesmoke;
    font-family: "Franklin Gothic Medium";
    font-size: x-large;
}